import {css} from '@emotion/core';
import styled from '@emotion/styled';
import {hideVisually} from '../../styles';
import './vars.css';

const base = () => css`
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
  background-color: transparent;
  border: 0;
  padding: 0;
  user-select: none;

  > input {
    ${hideVisually()};
  }
`;

const disabled = ({disabled: isDisabled}) =>
  isDisabled &&
  css`
    cursor: not-allowed;
    opacity: 0.5;
    transition: opacity 0.25s;
  `;

const track = () => css`
  position: relative;
  display: inline-block;
  width: 42px;
  height: 22px;
  border: 1px solid var(--recipe-toggle-track-border-color);
  border-radius: 16px;
  background-color: var(--recipe-alias-background-color-default);
  box-shadow: var(--recipe-alias-shadow-small);
  transition-property: background-color, border-color;
  transition-duration: 0.2s;

  input:focus + &,
  input:active + & {
    ::after {
      box-shadow: var(--recipe-alias-focus-ring-shadow);
    }
  }
  ::after {
    content: ' ';
    display: inline-block;
    position: absolute;
    top: 2px;
    left: 2px;
    width: 16px;
    height: 16px;
    border: 1px solid var(--recipe-toggle-track-border-color);
    border-radius: 50%;
    background-color: white;
    transition-property: border-color, left;
    transition-duration: 0.2s;
    box-sizing: border-box;
  }
  ::before {
    content: ' ';
    display: inline-block;
    position: absolute;
    width: 100%;
    height: 100%;
    background-position: 80% center;
    background-repeat: no-repeat;
    background-image: url(data:image/svg+xml;base64,);
  }
  input:checked + & {
    border-color: var(--recipe-toggle-border-color-selected);
    background-color: var(--recipe-toggle-background-color-selected);

    ::after {
      border-color: var(--recipe-toggle-border-color-selected);
      left: 50%;
    }

    ::before {
      background-position: 20% center;
      background-image: url(data:image/svg+xml;base64,);
    }
  }
`;

export const Container = styled.div<any>(base, disabled);
export const Track = styled.div<any>(track);
